.mensaje-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px auto;
    background-color: #f7f7f7; /* Fondo suave */
    max-height: 700px;
    padding: 10px;
}

.mensaje-container__titulo-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.mensaje-container__fuerzas {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

.mensaje-container__fuerzas img {
    width: 90px; /* Ajusta el tama√±o de las im√°genes */
    height: 90px;
    transition: opacity 0.3s ease, transform 0.3s ease; /* Transici√≥n suave para cambios */
}

.mensaje-container__fuerzas img.opacada {
    opacity: 0.2; /* Opaquear las im√°genes que no correspondan */
  }
  
  .mensaje-container__fuerzas img.resaltada {
    opacity: 1; /* Resaltar la imagen correspondiente */
    transform: scale(1.3); /* Puedes aplicar un zoom para resaltar m√°s */
  }


.mensaje-container__foto-container {
    display: flex;
    justify-content: center; /* Centra horizontalmente */
    margin-bottom: 20px; /* Espacio inferior */
}

.mensaje-container__foto {
    width: 200px; /* Ancho fijo */
    height: 250px; /* Alto fijo */
    object-fit: cover; /* Mantiene la proporci√≥n de la imagen */
    border-radius: 50%; /* Hace que la imagen sea circular */
    border: 5px solid #3498db; /* Cambia el color y grosor aqu√≠ */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); /* Sombra para un efecto elegante */
}

.mensaje-container__logo {
    width: 40px;
    height: 50px;
    margin-top: 5px;
    margin-bottom: 0;
}

.mensaje-container__nombre {
    margin-bottom: 5px;
    margin-top: 0;
}

.mensaje-container__slogan {
    margin-top: 0;
}

.mensaje-container__lista {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
}

.mensaje-container__item {
    font-size: 22px; /* Tama√±o del texto */
    display: flex; /* Usamos flex para que el contenido se alinee */
    align-items: center; /* Centra verticalmente */
    margin-bottom: 5px; /* Espacio entre los elementos */
}

/* Aseguramos que el li que contiene la foto tambi√©n est√© centrado */
.mensaje-container__item.foto-item {
    display: flex;
    justify-content: center; /* Centra la imagen dentro del li */
    align-items: center; /* Alinea verticalmente */
    text-align: center;
    margin-bottom: 5px; /* Espacio entre los elementos */
}
.mensaje-container__item span{
    width: 120px; /* Ancho fijo para el span */
    font-weight: bold;
    margin-right: 10px; /* Espacio entre el span y el texto */
    text-align: left;
}

.mensaje-container__registro {
    margin-top: 10px;
}